@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios {
    @include m.tablet {
        display: grid;
        gap: 2rem;
        @include m.grid(2, 2rem);

    }

  .servicio {
        background-color: v.$blanco;
        padding: 3rem;
        border-radius: 1rem;
        transition-property: transform;
        transition-duration: 0.3s;
        text-align: center;

        &.seleccionado {
            background-color: v.$naranja;
            p {
                color: v.$blanco;
            }
        }

        &:hover {
            transform: scale(1.1);
        }

        p {
            color: v.$negro;
            margin: 0 0 calc(v.$separacion / 4) 0;

        }

        .precio-servicio {
            color: v.$naranja;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }
    
  }
    
}